.App {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/* ローディング画面 */
.loading-screen {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.loading-spinner {
  text-align: center;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-top: 4px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.loading-spinner p {
  font-size: 18px;
  margin: 0;
}

/* Clean Map App Layout */
.clean-map-app {
  display: block !important;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/* Page Navigation */
.page-navigation {
  position: fixed;
  top: 20px;
  right: 20px;
  z-index: 1000;
}

.nav-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 20px;
  border-radius: 25px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.nav-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

.nav-button:active {
  transform: translateY(0);
}

.clean-map-button {
  background: linear-gradient(135deg, #22c55e 0%, #16a34a 100%);
  box-shadow: 0 4px 15px rgba(34, 197, 94, 0.3);
}

.clean-map-button:hover {
  box-shadow: 0 6px 20px rgba(34, 197, 94, 0.4);
}

/* レスポンシブ対応 */
@media (max-width: 768px) {
  .App {
    flex-direction: column;
  }
  
  .page-navigation {
    top: 10px;
    right: 10px;
  }
  
  .nav-button {
    padding: 10px 16px;
    font-size: 13px;
  }
}

@media (max-width: 480px) {
  .nav-button {
    padding: 8px 12px;
    font-size: 12px;
  }
}
